.wallet-banner {
  width: 100%;
  background: url("../img/connect-wallet-1920.jpg") center center no-repeat;
  background-size: cover;
  min-height: 784px;
  color: $white;
  padding: 160px 0 80px;
  position: relative;
  @include media-breakpoint-down(md) {
    background: url("../img/wallet-bg-mobile.jpg") center center no-repeat;
    background-size: cover;
  }
  &__description {
    margin-bottom: 40px;
  }
  &__balance {
    font-weight: 700;
    margin-bottom: 44px;
  }
  &__btn-row {
    display: flex;
    align-items: center;
    @include media-breakpoint-down(xl) {
      flex-direction: column;
    }
    .btn {
      @include media-breakpoint-down(xl) {
        width: 100%;
      }
      margin-right: 16px;
      @include media-breakpoint-down(xl) {
        margin-right: 0;
        margin-bottom: 15px;
      }
      &:last-child {
        margin-right: 0;
      }
    }
  }
}

.statistic {
  @include media-breakpoint-down(xl) {
    margin-top: 50px;
  }
  &__wrapper {
    display: flex;
    flex-direction: column;
    background: rgba(99, 98, 97, 0.2);
    border: 1px solid #000515;
    backdrop-filter: blur(17px);
    padding: 40px 38px;
  }
  &__content-row {
    text-align: center;
  }
}
