.main-banner {
  width: 100%;
  background: url("../img/main-page-banner.jpg") center center no-repeat;
  background-size: cover;
  min-height: 784px;
  color: $white;
  padding: 254px 0 32px;
  @include media-breakpoint-down(md) {
    background: url("../img/main-banner-bg-mobile.jpg") center center no-repeat;
    background-size: cover;
  }
  &__title {
    margin-bottom: 24px;
  }
  &__description {
    max-width: 377px;
    margin: 0 auto 40px auto;
    text-align: center;
  }
  &__btn-row {
    max-width: 480px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .btn {
      margin: 8px;
      @include media-breakpoint-down(sm) {
        width: 100%;
      }
    }
    @include media-breakpoint-up(sm) {
      flex-direction: row;
    }
  }
  .coin-info {
    width: 100%;
    max-width: 480px;
    margin: 0 auto 45px auto;
  }
  &__join {
    margin-top: 69px;
    &-title {
      font-weight: 700;
      font-size: 18px;
      line-height: 120%;
      margin-bottom: 21px;
    }
    &-list {
      display: inline-flex;
      align-items: center;
      padding-left: 0;
      margin-bottom: 0;
      list-style: none;
    }
    &-item {
      margin: 0 12px;
    }
    &-link {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      border: 1px dashed #828282;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      flex-shrink: 0;
      color: #828282;
      text-decoration: none;
      transition: all 0.3s ease;
      &:hover {
        border: 1px dashed $white;
        color: $white;
      }
    }
  }
}
