.btn {
  height: 49px;
  border-radius: 0;
  padding: 0 40px;
  font-weight: 400;
  font-size: 16px;
  line-height: 120%;
  border: 1px solid transparent;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  background: $black;
  color: $white;
  &.active,
  &:hover {
    border: 1px solid $blue !important;
    background: $blue;
    color: $white !important;
  }
  &.light {
    height: 38px;
    padding: 0 36px;
  }
  &.bold {
    font-weight: 700;
  }
  &.border {
    background: transparent;
    color: $black;
    border: 1px solid $black !important;
    &.active,
    &:hover {
      border: 1px solid $blue !important;
      background: $blue;
      color: $white !important;
    }
  }
  &.white {
    color:  $blue;
    background: $white;
    &.active,
    &:hover {
      border: 1px solid $blue !important;
      background: $blue;
      color: $white;
    }
    &.border {
      background: transparent;
      border: 1px solid $white !important;
      color: $white;
      &.active,
      &:hover {
        border: 1px solid $blue !important;
        background: $blue;
      }
    }
  }
}
