.collateral-slider {
  width: 60vw;
  @include media-breakpoint-down(lg) {
    margin-top: 40px;
    width: 100vw;
  }
  &__item {
    display: inline-flex !important;
    &:nth-child(2) {
      background: $black;
      color: $white;
    }
  }
  &__controls {
    display: flex;
    align-items: center;
    button {
      border: none;
      height: 4px;
      width: 35px;
      background: $white;
      margin-right: 12px;
      &.tns-nav-active {
        background: $purple;
      }
    }
  }
  &__wrapper {
    display: flex;
  }
}
.slider-card {
  display: flex;
  flex-direction: column;
  padding: 32px 34px;
  background: $white;
  border: 1px solid $black;
  width: 100%;
  &__icon {
    width: 36px;
    height: 36px;
    margin-bottom: 28px;
    img {
      width: 100px;
    }
  }
  &__title {
    margin-bottom: 16px;
  }
  &__text {
    margin-bottom: 0;
  }
  &.black {
    background: $black url("../img/black-slide-bg.png") no-repeat top right;
    color: $white;
  }
}
