@if ($use-title-palette) {
	@include titleSizes();
}

.text-block,
.remove-last-child-margin {
	> * {
		&:last-child {
			margin-bottom: 0;
		}
	}

	&.row {
		> * {
			&.col,
			&[class^="col-"] {
				&:last-child {
					> * {
						margin-bottom: 0;
					}
				}
			}
		}
	}
}

.text-sm {
	font-size: $font-size-sm !important;
	line-height: $line-height-sm;
}

.text-lg {
	font-size: $font-size-lg !important;
	line-height: $line-height-lg;
}


h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
	font-weight: 700;
	line-height: 120%;
	.highlight {
		position: relative;
		display: inline-block;
		z-index: 1;
		&:before {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			bottom: 3px;
			height: 20px;
			background: rgba(154, 31, 161, 0.4);
			width: 100%;
			z-index: -1;
			@include media-breakpoint-up(lg) {
				bottom: 10px;
			}
		}
	}
}

h1, .h1 {
	@include media-breakpoint-up(lg) {
		font-size: 60px;
	}
}

h2, .h2 {
	@include media-breakpoint-up(lg) {
		font-size: 56px;
	}
}

h3, .h3 {
	@include media-breakpoint-up(lg) {
		font-size: 36px;
	}
}

h4, .h4 {
	@include media-breakpoint-up(lg) {
		font-size: 24px;
	}
}

h5, .h5 {
	@include media-breakpoint-up(lg) {
		font-size: 18px;
	}
}

h6, .h6 {
	@include media-breakpoint-up(lg) {
		font-size: 16px;
	}
}
