.tokenomics {
  padding: 140px 0;
  color: $white;
  @include media-breakpoint-down(lg) {
    padding: 70px 0;
  }
  &__title {
    margin-bottom: 16px;
  }
  &__description {
    text-align: center;
    max-width: 425px;
    margin: 0 auto 53px auto;
    color: $white;
  }
  .chart {
    display: flex;
    width: 100%;
    justify-content: center;
    @include media-breakpoint-down(lg) {
      flex-direction: column;
      align-items: center;
    }
    &__doughnut {
      width: 100%;
      max-width: 457px;
      background: url("../img/chart-bg.svg") no-repeat center center;
      background-size: 110px auto;
      @include media-breakpoint-down(lg) {
        margin-bottom: 50px;
      }
      @include media-breakpoint-down(sm) {
        background-size: 80px auto;
      }
    }
    &__data {
      width: 100%;
      max-width: 342px;
      margin-left: 110px;
      @include media-breakpoint-down(lg) {
        margin-left: 0;
      }
      display: flex;
      flex-direction: column;
      &-title {
        margin-bottom: 28px;
      }
      &-list {
        display: flex;
        flex-direction: column;
        padding-left: 0;
      }
      &-item {
        display: flex;
        align-items: center;
        margin-bottom: 21px;
        &:last-child {
          margin-bottom: 0;
        }
        .icon {
          width: 22px;
          height: 22px;
          border-radius: 50%;
          border: 5px solid transparent;
          margin-right: 16px;
          flex-shrink: 0;
        }
        .name {
          font-weight: 400;
          font-size: 18px;
          line-height: 140%;
        }
        .val {
          margin-left: auto;
          font-weight: 700;
          font-size: 18px;
          line-height: 140%;
          color: $gray-blue;
        }
      }
    }
  }
}
