.to-top {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 100px;
  right: 100px;
  border: 1px solid $white;
  cursor: pointer;
  @include media-breakpoint-down(md) {
    right: 40px;
  }
  i {
    position: absolute;
    transform: rotate(-90deg);
    color: $white;
    font-size: 10px;
  }
}
