.token-details {
  padding: 140px 0;
  @include media-breakpoint-down(lg) {
    padding: 70px 0;
  }
  &__wrapper {
    border: 1px solid $black;
    background: $white;
  }
  &__top-row {
    border-bottom: 1px solid rgba(0, 5, 21, 0.23);
    padding: 40px;
    display: flex;
    align-items: center;
    @include media-breakpoint-down(md) {
      padding: 20px;
      flex-direction: column;
    }
  }
  &__header {
    width: 45%;
    @include media-breakpoint-down(md) {
      width: 100%;
    }
  }
  &__bonus {
    display: flex;
    align-items: center;
    @include media-breakpoint-down(md) {
      justify-content: space-between;
    }
    .item {
      display: inline-flex;
      flex-direction: column;
      margin-right: 104px;
      position: relative;
      color: $black2;
      @include media-breakpoint-down(md) {
        margin-right: 60px;
      }
      &:before {
        content: '';
        width: 1px;
        height: 80px;
        background: rgba(0, 5, 21, 0.23);
        position: absolute;
        right: -52px;
        top: 50%;
        transform: translateY(-50%);
        @include media-breakpoint-down(md) {
          display: none;
        }
      }
      &:last-child {
        margin-right: 0;
        &:before {
          display: none;
        }
      }
      &__val {
        font-weight: 700;
        font-size: 48px;
        line-height: 120%;
        @include media-breakpoint-down(md) {
          font-size: 35px;
        }
      }
      &__text {
        font-weight: 700;
        font-size: 16px;
        line-height: 140%;
      }
    }
  }

  .data-container {
    display: flex;
    padding: 40px;
    @include media-breakpoint-down(xl) {
      padding: 20px;
    }
    @include media-breakpoint-down(md) {
      flex-direction: column;
    }
    .data-list {
      list-style: none;
      padding-left: 0;
      margin-bottom: 0;
      width: 100%;
      max-width: 316px;
      margin-left: 64px;
      @include media-breakpoint-down(md) {
        margin-left: 0;
      }
      &:first-child {
        margin-left: 0;
        @include media-breakpoint-down(md) {
          margin-bottom: 24px;
        }
      }
      &__item {
        display: flex;
        align-items: center;
        width: 100%;
        margin-bottom: 24px;
        &:last-child {
          margin-bottom: 0;
        }
        &-name {
          width: 50%;
          padding-right: 10px;
          font-weight: 400;
          font-size: 20px;
          line-height: 140%;
          @include media-breakpoint-down(xl) {
            font-size: 16px;
          }
        }
        &-val {
          font-size: 16px;
          line-height: 140%;
          color: $main-gray;
          @include media-breakpoint-down(xl) {
            font-size: 15px;
          }
        }
      }
    }
  }
  .mechanism {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 40px;
    background: $gray-400;
    &__list {
      padding-left: 0;
      margin-bottom: 0;
      &-item {
        margin-bottom: 25px;
        font-size: 18px;
        line-height: 160%;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  .footer {
    color: $white;
    background: $black;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 28px;
    @include media-breakpoint-down(md) {
      flex-direction: column;
    }
    &__title {
      font-size: 20px;
      line-height: 140%;
      @include media-breakpoint-down(md) {
        margin-bottom: 10px;
      }
    }
    &__list {
      list-style: none;
      display: flex;
      align-items: center;
      padding-left: 0;
      margin-bottom: 0;
      &-item {
        display: inline-flex;
        align-items: center;
        margin-left: 28px;
        @include media-breakpoint-down(lg) {
          margin-left: 18px;
        }
        @include media-breakpoint-down(md) {
          margin-left: 8px;
        }
        .icon {
          width: 28px;
          height: 28px;
          margin-right: 8px;
          flex-shrink: 0;
          display: inline-flex;
          @include media-breakpoint-down(md) {
            margin-right: 5px;
            width: 20px;
            height: 20px;
          }
          img {
            width: 100%;
          }
        }
        .name {
          color: $white;
          opacity: 0.7;
          @include media-breakpoint-down(md) {
            font-size: 14px;
          }
        }
      }
    }
  }
}
