@import "variables";

@font-face {
  font-family: '#{$icomoon-font-family}';
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?vlhetn');
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?vlhetn#iefix') format('embedded-opentype'),
  url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff2?vlhetn') format('woff2'),
  url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?vlhetn') format('truetype'),
  url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?vlhetn') format('woff'),
  url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?vlhetn##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icomoon-"], [class*=" icomoon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icomoon-reddit {
  &:before {
    content: $icomoon-reddit;
  }
}
.icomoon-insta {
  &:before {
    content: $icomoon-insta;
  }
}
.icomoon-telegram {
  &:before {
    content: $icomoon-telegram;
  }
}
.icomoon-twitter {
  &:before {
    content: $icomoon-twitter;
  }
}
.icomoon-arrow-phase {
  &:before {
    content: $icomoon-arrow-phase;
  }
}
.icomoon-close {
  &:before {
    content: $icomoon-close;
  }
}
.icomoon-minus {
  &:before {
    content: $icomoon-minus;
  }
}
.icomoon-play {
  &:before {
    content: $icomoon-play;
  }
}
.icomoon-plus {
  &:before {
    content: $icomoon-plus;
  }
}
