.roadmap {
  margin-bottom: 140px;
  @include media-breakpoint-down(lg) {
    margin-bottom: 70px;
  }
  &__description {
    max-width: 647px;
    margin: 0 auto 70px auto;
  }
  &__container {
    background-image: url("../img/phase-bg.png"), url("../img/phase-bg.png"), url("../img/phase-bg.png");
    background-repeat: no-repeat, no-repeat, no-repeat;
    background-size: 36px 39px, 20px 22px, 41px 45px;
    background-position: 213px 80%, 43% 21%, 76% 95%;
    @include media-breakpoint-down(lg) {
      background: none;
    }
  }
  &__list {
    display: flex;
    width: 100%;
    flex-direction: column;
    padding-left: 0;
    max-width: 218px;
    margin: 0 auto;
    padding-top: 18px;
    border-top: 3px solid $purple;
    list-style: none;
    color: $main-gray;
    font-size: 16px;
    line-height: 200%;
    position: relative;
    &-item {
      padding-left: 25px;
      position: relative;
      &:before {
        content: '';
        background:  $main-gray;
        width: 3px;
        height: 3px;
        border-radius: 50%;
        position: absolute;
        left: 12px;
        top: 13px;
      }
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    padding-left: 0 !important;
    padding-right: 0 !important;
    margin-bottom: 40px;
    &-title {
      text-align: center;
      border-bottom: 2px solid $black;
      font-weight: 400;
      font-size: 24px;
      line-height: 140%;
      padding-bottom: 8px;
      margin-bottom: 112px;
      position: relative;
      &:after {
        font-family: "icomoon";
        content: "\e904";
        position: absolute;
        bottom: -55px;
        left: 50%;
        transform: translateX(-50%);
        font-size: 70px;
      }
    }
  }
}
