.coin-info {
  width: 100%;
  &__bar {
    width: 100%;
    height: 27px;
    border: 1px solid $black;
    background: $gray-dark;
    display: inline-flex;
    align-items: center;
    padding: 3px;
    &-status {
      height: 100%;
      background: linear-gradient(89.88deg, #621C7A 0.1%, #C922C2 99.97%);
    }
    &.white-border {
      border: 1px solid $white;
    }
  }
  &__exchange-rate {
    display: inline-flex;
    align-items: center;
    font-size: 14px;
    line-height: 21px;
    margin-bottom: 16px;
    span {
      &:last-child {
        color: $gray-light;
        margin-left: 12px;
      }
    }
  }
  &__data {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 4px;
    span {
      display: inline-block;
      font-weight: 600;
      font-size: 14px;
      line-height: 16px;
    }
  }
}
