.contact-us {
  padding: 140px 0;
  color: $white;
  background: url("../img/contact.jpg") center center no-repeat;
  background-size: cover;
  @include media-breakpoint-down(lg) {
    padding: 70px 0;
  }
  @include media-breakpoint-down(md) {
    background: url("../img/contact-mobile.jpg") center center no-repeat;
    background-size: cover;
  }
  &__description {
    color: $white;
    margin-bottom: 40px;
  }
  .form {
    .btn {
      width: 100%;
    }
  }
}
