.of-contain {
	@include absoluteCover;

	object-fit: contain;
}

.of-cover {
	@include absoluteCover;

	object-fit: cover;
}

.bold {
	font-weight: 700;
}

.img-wrapper {
	position: relative;
	img {
		width: 100%;
	}
	&:before {
		content: '';
		width: 100%;
		height: 100%;
		position: absolute;
		background: rgba(107, 29, 128, 0.2);
		right: -10px;
		top: -10px;
		z-index: -1;
	}
}
