.token-price {
  padding-bottom: 140px;
  color: $white;
  @include media-breakpoint-down(lg) {
    padding-bottom: 70px;
  }

  &__description {
    color: $white;
    margin-bottom: 72px;
  }

  .phase-item-list {
    display: flex;
  }

  .phase-item-container {
    padding-top: 50px;
    padding-bottom: 24px;
    margin-bottom: 45px;
    overflow-x: auto;
    @include customScrollbar(1px, #4F4F4F, #D9D9D9, 5px);
    @include media-breakpoint-up(sm) {
      width: calc(100vw - ((100vw - #{map-get($container-max-widths, sm)}) / 2));
    }
    @include media-breakpoint-up(md) {
      width: calc(100vw - ((100vw - #{map-get($container-max-widths, md)}) / 2));
    }
    @include media-breakpoint-up(lg) {
      width: calc(100vw - ((100vw - #{map-get($container-max-widths, lg)}) / 2));
    }
    @include media-breakpoint-up(xl) {
      width: calc(100vw - ((100vw - #{map-get($container-max-widths, xl)}) / 2));
    }
  }
}

.phase-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid $white;
  position: relative;
  height: 194px;
  padding: 20px;
  width: 100%;
  max-width: 260px;
  flex-shrink: 0;
  margin-right: 40px;

  &:last-child {
    margin-right: 0;
  }

  &__num {
    background: $white;
    width: 100px;
    height: 30px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-weight: 400;
    font-size: 16px;
    color: $black;
    position: absolute;
    top: -15px;
  }

  &__val {
    font-weight: 700;
    font-size: 44px;
    line-height: 120%;
    margin-bottom: 12px;
  }

  &__available {
    font-weight: 400;
    font-size: 18px;
    line-height: 120%;
  }
}
