.call-to-action {
  padding: 140px 0;
  background: $black2 url("../img/call-to-action.png") no-repeat center;
  color: $white;
  @include media-breakpoint-down(md) {
    background: $black2 url("../img/сall-to-action-bg-mobile.jpg") no-repeat center;
    background-size: cover;
  }
  &__description {
    color: $white;
    max-width: 647px;
    margin: 0 auto 70px auto;
  }
  &__control {
    position: relative;
  }
}
