.step2 {
  padding: 140px 0;
  position: relative;
  background-color: $violet;
  background-image: url('../img/phase-bg.png');
  background-repeat: no-repeat;
  background-size: 36px 39px;
  background-position: calc(50% - 270px) 140px;
  @include media-breakpoint-down(lg) {
    padding: 70px 0;
    background-image: none;
  }
  &:before {
    content: '';
    background: url("../img/step-line.svg") no-repeat center;
    background-size: contain;
    width: 160px;
    height: 353px;
    position: absolute;
    left: calc(50% - 500px);
    top: -250px;
    @include media-breakpoint-down(xl) {
      left: calc(50% - 440px);
    }
    @include media-breakpoint-down(lg) {
      left: calc(50% - 200px);
      width: 120px;
      height: 146px;
      top: -62px;
    }
  }
  &__description {
    margin-bottom: 70px;
  }
}

.step-item {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: $white;
  border: 1px solid $black;
  padding: 32px 24px;
  @include media-breakpoint-down(lg) {
    margin-bottom: 30px;
    height: auto;
  }
  &__icons {
    display: flex;
    align-items: center;
    margin-bottom: 28px;
    .item {
      width: 36px;
      height: 36px;
      margin-right: 12px;
      &:last-child {
        margin-right: 0;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
  }
  &__title {
    margin-bottom: 16px;
  }
  &__description {
    p {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
