.member-club {
  padding-bottom: 100px;
  @include media-breakpoint-down(lg) {
    padding-bottom: 30px;
  }
  &__item {
    padding: 16px;
    border: 1px solid $black2;
    background: $white;
    margin-bottom: 40px;
    &-title {
      font-weight: 700;
      font-size: 24px;
      line-height: 32px;
      margin-bottom: 16px;
    }
    &-description {
      font-size: 16px;
      line-height: 23px;
      margin-bottom: 16px;
    }
    &-investors {
      background: $violet;
      padding: 12px 16px;
      font-weight: 700;
      font-size: 16px;
      width: 100%;
      color: #6B1D80;
      margin-bottom: 8px;
      border: 1px solid #6B1D80;
      display: inline-flex;
      align-items: center;
    }
    &-text {
      font-weight: 400;
      font-size: 14px;
      margin-bottom: 16px;
    }
    &-list {
      list-style: none;
      padding-left: 0;
      margin-bottom: 0;
      display: flex;
      flex-direction: column;
      width: 100%;
      li {
        color: $main-gray;
        font-size: 16px;
        line-height: 200%;
        margin-bottom: 5px;
        padding-left: 20px;
        position: relative;
        &:before {
          content: '';
          width: 3px;
          height: 3px;
          background: $main-gray;
          border-radius: 50%;
          position: absolute;
          left: 8px;
          top: 14px;
        }
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}
