.collateral-network {
  padding: 140px 0;
  @include media-breakpoint-down(lg) {
    padding: 70px 0;
  }
  &__title {
    margin-bottom: 16px;
  }
  &__description {
    max-width: 377px;
  }
  &__buttons {
    display: flex;
    .btn {
      &:first-child {
        margin-right: 16px;
      }
    }
  }
  
  &__video-wrap {
    position: relative;
    @include media-breakpoint-down(lg) {
      margin-top: 70px;
    }
  }

  &__video-link {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    display: inline-flex;
    width: 64px;
    height: 64px;
    background: rgba(255,255,255, .2);
    border: 1px solid $white;
    align-items: center;
    justify-content: center;
    color: $white;
    i {
      font-size: 18px;
      position: relative;
      right: -2px;
    }
    &:hover {
      background: rgba(255,255,255, .3);
    }
  }
}
