.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 15px 0;
  z-index: 2;
  @include media-breakpoint-up(lg) {
    padding: 35px 0;
  }
  .logo {
    display: inline-flex;
  }
  .nav {
    margin-right: 28px;
    @include media-breakpoint-down(lg) {
      display: flex;
      flex-direction: column;
      margin-right: 0;
    }
    &__item {
      margin-left: 36px;
      @include media-breakpoint-down(lg) {
        margin-left: 0;
        margin-bottom: 40px;
      }
      &:first-child {
        margin-left: 0;
      }
      &-link {
        font-weight: 400;
        font-size: 16px;
        line-height: 120%;
        color: $white;
        text-decoration: none;
        transition: all 0.3s ease;
        @include media-breakpoint-down(lg) {
          color: $black;
        }
        &:hover {
          text-decoration: underline;
        }
      }
    }
  }
  .hamburger-menu {
    margin-left: auto;
    position: relative;
    z-index: 1050 !important;
  }
  &.black {
    .nav {
      &__item-link {
        color: $black;
      }
    }
    .btn {
      .white {
        background: #CCCCCC;
      }
      &.border {
        background: transparent;
        color: $black;
        border: 1px solid $black !important;
      }
    }
    .hamburger-menu {
      &__bars {
        background: $black;
        &:after,
        &:before {
          background: $black;
        }
      }
    }
  }
}

.header-nav {
  .btn {
    margin-bottom: 20px;
  }
  @include media-breakpoint-down(lg) {
    padding: 35px 0;
    display: flex;
    align-items: center !important;
    justify-content: center !important;
    .btn {
      color: $black !important;
      border: 1px solid $black !important;
      &.border.white {
        color: $black !important;
        border: 1px solid $black !important;
      }
    }
  }
  @include media-breakpoint-up(lg) {
    .btn {
      margin-left: 16px;
      margin-bottom: 0;
    }
  }
}
