.modal {
  .modal-content {
    .btn-close {
      position: absolute;
      right: 12px;
      top: 12px;
      font-size: 14px;
    }
  }
  &.top-text {
    .modal-content {
      background: transparent;
    }
  }
  &__top {
    text-align: center;
    margin-bottom: 44px;
    color: $white;
  }
  &__body {
    background: $white;
    position: relative;
    padding: 40px;
  }
}

.modal-backdrop {
  background: rgba(22, 22, 22, 0.85);
  backdrop-filter: blur(7px);
  --bs-backdrop-opacity: 1 !important;
}

.success-send {
  &__body {
    display: flex;
    flex-direction: column;
    padding: 28px;
    align-items: center;
    justify-content: center;
  }
  &__icon {
    width: 44px;
    height: 44px;
    margin-bottom: 20px;
    img {
      width: 100%;
    }
  }
  &__title {
    margin-bottom: 16px;
    .highlight {
      &:before {
        bottom: 4px;
      }
    }
  }
  &__description {
    font-size: 16px;
    line-height: 26px;
    color: $main-gray;
    max-width: 340px;
    margin: 0 auto;
    text-align: center;
  }
}

.video-modal {
  .btn-close {
    position: absolute;
    right: -30px !important;
    top: -20px !important;
    background-color: $white;
    border-radius: 50%;
    border: 1px solid $black;
    font-size: 10px !important;
    padding: 6px;
  }
  &__video {
    width: 100%;
  }
}

.exchange-form {
  .btn-close {
    top: 40px;
    right: 40px;
  }
  &__title {
    margin-bottom: 24px;
  }
  &__wrap {
    .control-wrapper {
      &__label {
        font-weight: 700;
        font-size: 16px;
        line-height: 160%;
        margin-bottom: 4px;
      }
      .form-control {
        background: $gray-600;
        color: $black;
        &:focus {
          box-shadow: none !important;
        }
      }
      .currency {
        display: inline-flex;
        align-items: center;
        position: absolute;
        right: 10px;
        top: 40px;
        &__icon {
          width: 24px;
          height: 24px;
          margin-right: 8px;
          img {
            width: 100%;
            height: 100%;
            object-fit: contain;
          }
        }
        &__name {
          padding-top: 2px;
        }
      }
    }
  }
}

.status-modal {
  .modal__body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__icon {
    width: 85px;
    height: 85px;
    margin-bottom: 32px;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  &__title {
    margin-bottom: 16px;
  }
  &__description {
    text-align: center;
    max-width: 416px;
    margin: 0 auto;
  }
  &__buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 31px;
    margin-top: 40px;
    width: 100%;
    .btn {
      width: 100%;
      max-width: 155px;
      margin: 0 8px;
    }
  }
  &__link {
    text-align: center;
    a {
      font-weight: 700;
      font-size: 16px;
      line-height: 120%;
    }
  }
}
