.form-control {
  height: 50px;
  border-radius: 0px;
  border: transparent;
  background: $gray-dark;
  color: $white;
  font-size: 16px;
  padding-left: 24px;
  padding-right: 24px;
  &:focus {
    background: $gray-dark;
    color: $white;
    border: 1px solid $white;
  }
  &:disabled {
    background: $gray-dark;
    color: #8F8F8F;
  }
  &::placeholder {
    color: $gray-500;
  }
  &.is-invalid {
    border: 1px solid $red;
    background-image: none;
    color: $red;
  }
}
.form-select {
  height: 50px;
  border-radius: 0px;
  border: transparent;
  background: $gray-dark url("../img/icons/arrow_drop_down.svg") no-repeat calc(100% - 15px) center;
  color: $white;
  font-size: 16px;
  cursor: pointer;
  &:focus {
    background: $gray-dark url("../img/icons/arrow_drop_down.svg") no-repeat calc(100% - 15px) center;
    color: $white;
    border: 1px solid $white;
  }
  &:disabled {
    background: $gray-dark url("../img/icons/arrow_drop_down.svg") no-repeat calc(100% - 15px) center;
    color: #8F8F8F;
  }
}

.was-validated :invalid ~ .invalid-feedback,
.was-validated :invalid ~ .invalid-tooltip,
.is-invalid ~ .invalid-feedback,
.is-invalid ~ .invalid-tooltip {
  position: absolute;
  color: $red;
  bottom: -25px;
}
.control-wrapper {
  position: relative;
  margin-bottom: 25px;
}
.iti {
  &--allow-dropdown {
    width: 100%;
  }
  &__country-name {
    color: $black;
  }
  &__arrow {
    border-top: 4px solid $white;
    opacity: 0.8;
  }
  &__arrow--up {
    border-top: none !important;
    border-bottom: 4px solid $white;
    opacity: 0.8;
  }
}

.form-check {
  margin-bottom: 25px;
  .form-check-input {
    width: 20px;
    height: 20px;
    background: transparent;
    border-radius: 0;
    border: 1px solid $white;
    cursor: pointer;
    &:checked[type=checkbox] {
      background-color: $white;
      background-image: url("../img/icons/checkmark.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 15px 15px;
    }
  }
  .form-check-label {
    margin-left: 5px;
    padding-top: 3px;
  }
}
