.tokens-features {
  margin-bottom: 140px;
  @include media-breakpoint-down(lg) {
    margin-bottom: 70px;
  }
  &__title {
    margin-bottom: 72px;
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 254px;
    margin: auto;
    @include media-breakpoint-down(lg) {
      max-width: 100%;
      margin-bottom: 20px;
    }
    &-icon {
      width: 44px;
      height: 44px;
      margin-bottom: 28px;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
    &-title {
      font-weight: 800;
      margin-bottom: 16px;
    }
    &-description {
      color: $main-gray;
      font-size: 16px;
    }
  }
}
