.swappable-features {
  padding-bottom: 100px;
  &__list {
    height: 309px;
    background: url("../img/bg-lines.png") no-repeat center center;
    background-size: cover;
    @include media-breakpoint-down(lg) {
      height: 180px;
    }
    &-wrapper {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    &-item {
      position: absolute;
      width: 90px;
      height: 90px;
      @include media-breakpoint-down(lg) {
        width: 60px;
        height: 60px;
      }
      @include media-breakpoint-down(md) {
        width: 50px;
        height: 50px;
      }
      img {
        width: 100%;
      }
      &:first-child {
        left: 20px;
        top: 45px;
        @include media-breakpoint-down(lg) {
          top: 25px;
        }
        @include media-breakpoint-down(md) {
          top: 35px;
        }
        @include media-breakpoint-down(sm) {
          top: 62px;
        }
      }
      &:last-child {
        right: 20px;
        top: 45px;
        @include media-breakpoint-down(lg) {
          top: 25px;
        }
        @include media-breakpoint-down(md) {
          top: 45px;
        }
        @include media-breakpoint-down(sm) {
          top: 62px;
        }
      }
      &:nth-child(2) {
        left: calc(25% - 45px);
        top: 100px;
        @include media-breakpoint-down(lg) {
          left: 25%;
          top: 50px;
        }
        @include media-breakpoint-down(md) {
          left: 23%;
          top: 80px;
        }
      }
      &:nth-child(3) {
        left: calc(50% - 45px);
        top: 155px;
        @include media-breakpoint-down(lg) {
          left: calc(50% - 30px);
          top: 85px;
        }
        @include media-breakpoint-down(md) {
          left: calc(50% - 30px);
          top: 90px;
        }
      }
      &:nth-child(4) {
        left: calc(75% - 45px);
        top: 100px;
        @include media-breakpoint-down(lg) {
          left: calc(75% - 30px);
          top: 65px;
        }
        @include media-breakpoint-down(md) {
          left: calc(75% - 50px);
          top: 75px;
        }
        @include media-breakpoint-down(sm) {
          top: 85px;
        }
      }
    }
  }
}
