.features {
  padding-bottom: 140px;
  @include media-breakpoint-down(lg) {
    padding-bottom: 70px;
  }
  &__description {
    max-width: 341px;
    margin-bottom: 40px;
  }
  &__list {
    display: flex;
    flex-direction: column;
  }
  &__item {
    display: flex;
    margin-bottom: 12px;
    &-icon {
      flex-shrink: 0;
      width: 24px;
      margin-right: 16px;
      img {
        width: 100%;
      }
    }
    &-title {
      margin-bottom: 12px;
    }
    &-description {
      font-size: 16px;
      color: $main-gray;
    }
  }
  &__images {
    @include media-breakpoint-down(lg) {
      margin-top: 90px;
    }
    .img-wrapper {
      &:first-child {
        margin-bottom: 34px;
      }
    }
  }
}
