.buy-banner {
  width: 100%;
  background: url("../img/how_to_buy_banner.jpg") center center no-repeat;
  background-size: cover;
  min-height: 784px;
  color: $white;
  padding: 120px 0;
  position: relative;
  @include media-breakpoint-down(md) {
    background: $black2 url("../img/how_to_buy_mobile.jpg") no-repeat center;
    background-size: cover;
  }
  &__title {
    margin-bottom: 24px;
  }
  &__description {
    max-width: 377px;
    margin: 0 auto 40px auto;
    text-align: center;
  }
  .coin-info {
    width: 100%;
    max-width: 480px;
    margin: 0 auto 45px auto;
  }
  .read-more {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: absolute;
    bottom: 65px;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;
    i {
      font-size: 30px;
      height: 20px;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      align-items: end;
      justify-content: end;
    }
  }
}
