.question {
  margin-bottom: 140px;
  @include media-breakpoint-down(lg) {
    margin-bottom: 70px;
  }
  &__description {
    max-width: 647px;
    margin: 0 auto 70px auto;
  }
}
.nav-question {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 72px;
  @include media-breakpoint-down(lg) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
  }
  @include media-breakpoint-down(sm) {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 20px;
  }
  .nav-item {
    margin: 0 13px;
    @include media-breakpoint-down(lg) {
      margin: 0;
      .btn {
        width: 100%;
      }
    }
  }
}
