.assets-for-borrow {
  padding: 140px 0 100px 0;
  @include media-breakpoint-down(lg) {
    padding: 70px 0 30px;
  }
  &__description {
    max-width: 568px;
    margin: 0 auto 72px auto;
  }
}

.assets-item {
  display: flex;
  flex-direction: column;
  background: $white;
  border: 1px solid $black;
  padding: 24px;
  margin-bottom: 40px;
  height: calc(100% - 40px);
  &__icon {
    width: 28px;
    height: 28px;
    margin-bottom: 12px;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  &__title {
    margin-bottom: 8px;
  }
  &__description {
    color: $main-gray;
    margin-bottom: 0;
  }
}
