.accordion {
  background: transparent !important;
  border: none !important;
  border-radius: 0 !important;
  .accordion-item {
    background: transparent;
    border-radius: 0 !important;
    border-left: none;
    border-right: none;
    border-top: none;
  }
  .accordion-button {
    border: none !important;
    border-radius: 0 !important;
    background: transparent !important;
    outline: none !important;
    box-shadow: none !important;
    padding: 20px 0;
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
    &:after {
      background: none;
      font-family: 'icomoon';
      content: "\e903";
      transform: translate(0);
      font-size: 20px;
    }
    &.collapsed {
      &:after {
        background: none;
        font-family: 'icomoon';
        content: "\e901";
        transform: translate(0);
        font-size: 20px;
      }
    }
  }
  .accordion-body {
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 20px;
    padding-top: 0;
    color: $main-gray;
    font-size: 16px;
  }
}
